                </div>
                <div class="mykey-top-bottom">
                    <div>
                        <div class="mykey-top-bottom-opacity">我的KEY</div>
                        <div>{{ user.k1 }}</div>
                    </div>
                    <div>
                        <div class="mykey-top-bottom-opacity">权重占比</div>
                        <div>{{ user.r1 }}%</div>
                    </div>
                </div>
                <div class="mykey-top">
                    <div class="mykey-top-left" style="width: 50%;margin: 0;">
                        <div class="mykey-top-bottom-opacity">我的收益</div>
                        <div>{{ user.p1 }}</div>
                    </div>
                    <div class="mykey-top-right" style="width: 50%;">
                        <div class="djtnew-lottery-button" style="margin-top: 0;" @click="profitSubmit($event,0)">提取</div>
                    </div>
                </div>
            </div>
            <div class="djtnew-top mykey-marginTop">
                <div class="mykey-top">
                    <div class="mykey-top-left">
                        <div class="mykey-top-left-title">马斯克队</div>
                        <div class="mykey-top-left-desc">全网KEY:{{ user.t2 }}</div>
                    </div>
                    <div class="mykey-top-right">
                        <img src="../../assets/img/tab2.png" alt="">
                    </div>
                </div>
                <div class="mykey-top-bottom">
                    <div>
                        <div class="mykey-top-bottom-opacity">我的KEY</div>
                        <div>{{ user.k2 }}</div>
                    </div>
                    <div>
                        <div class="mykey-top-bottom-opacity">权重占比</div>
                        <div>{{ user.r2 }}%</div>
                    </div>
                </div>
                <div class="mykey-top">
                    <div class="mykey-top-left" style="width: 50%;margin: 0;">
                        <div class="mykey-top-bottom-opacity">我的收益</div>
                        <div>{{ user.p2 }}</div>
                    </div>
                    <div class="mykey-top-right" style="width: 50%;">
                        <div class="djtnew-lottery-button" style="margin-top: 0;" @click="profitSubmit($event,1)">提取</div>
                    </div>
                </div>
            </div>
            <div class="djtnew-top mykey-marginTop">
                <div class="mykey-top">
                    <div class="mykey-top-left">
                        <div class="mykey-top-left-title">自由女神队</div>
                        <div class="mykey-top-left-desc">全网KEY:{{ user.t3 }}</div>
                    </div>
                    <div class="mykey-top-right">
                        <img src="../../assets/img/tab3.png" alt="">
                    </div>
                </div>
                <div class="mykey-top-bottom">
                    <div>
                        <div class="mykey-top-bottom-opacity">我的KEY</div>
                        <div>{{ user.k3 }}</div>
                    </div>
                    <div>
                        <div class="mykey-top-bottom-opacity">权重占比</div>
                        <div>{{ user.r3 }}%</div>
                    </div>
                </div>
                <div class="mykey-top">
                    <div class="mykey-top-left" style="width: 50%;margin: 0;">
                        <div class="mykey-top-bottom-opacity">我的收益</div>
                        <div>{{ user.p3 }}</div>
                    </div>
                    <div class="mykey-top-right" style="width: 50%;">
                        <div class="djtnew-lottery-button" style="margin-top: 0;" @click="profitSubmit($event,2)">提取</div>
                    </div>
                </div>
            </div>
            <div class="djtnew-top mykey-marginTop">
                <div class="mykey-top">
                    <div class="mykey-top-left">
                        <div class="mykey-top-left-title">拜登队</div>
                        <div class="mykey-top-left-desc">全网KEY:{{ user.t4 }}</div>
                    </div>
                    <div class="mykey-top-right">
                        <img src="../../assets/img/tab4.png" alt="">
                    </div>
                </div>
                <div class="mykey-top-bottom">
                    <div>
                        <div class="mykey-top-bottom-opacity">我的KEY</div>
                        <div>{{ user.k4 }}</div>
                    </div>
                    <div>
                        <div class="mykey-top-bottom-opacity">权重占比</div>
                        <div>{{ user.r4 }}%</div>
                    </div>
                </div>
                <div class="mykey-top">
                    <div class="mykey-top-left" style="width: 50%;margin: 0;">
                        <div class="mykey-top-bottom-opacity">我的收益</div>
                        <div>{{ user.p4 }}</div>
                    </div>
                    <div class="mykey-top-right" style="width: 50%;">
                        <div class="djtnew-lottery-button" style="margin-top: 0;" @click="profitSubmit($event,3)">提取</div>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="meanShow" class="index_pop_content">
            <div class="index_pop_top">
                <div>
                    <img src="../../assets/img/logo.png" alt="" class="index_pop_top-left">
                </div>
                <div @click="closeMean">
                    <img src="../../assets/img/mean-close.png" alt="" class="index_pop_top-right">
                </div>
            </div>
            <div class="index_pop_main_tab">
                <div class="index_pop_middle"  @click="toIndex()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/img/mean-home.png" alt="">
                        <span>首页</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toOrder()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/img/mean-order.png" alt="">
                        <span>我的KEY</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toTrade()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/img/mean-trade.png" alt="">
                        <span>交易</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toInvite">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/img/mean-invite.png" alt="">
                        <span>我的邀请</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toWhite">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/img/mean-book.png" alt="">
                        <span>游戏白皮书</span>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="maskShow" class="index_mask">
            <div class="index_mask-content" @click="checkNet()">
                <div class="index_mask-title">{{ lang_list.index_mask }}</div>
                <img src="../../assets/img/mask.png" alt="" class="index_mask-img">
            </div>
            <div @click="closeMask()">
                <img src="../../assets/img/close.png" alt="" class="index_mask-close">
            </div>
        </div>
        <div v-if="langShow" class="index_lang">
            <div :class="lang=='zh'?'index_lang-button-active':'index_lang-button'" @click="switchLang('zh')">中文</div>
            <div :class="lang=='en'?'index_lang-button-active':'index_lang-button'" @click="switchLang('en')">EN</div>
        </div>
    </div>
</template>
<script>
    import {WOW} from 'wowjs';
    let provider, signer, defaultAccount, networkVersion;
    let conContract,djtContract;
    export default {
        data() {
            return {
                lang:'zh',
                lang_list:{},
                langShow:false,
                address: '',
                short_address: 'connect',
                p_address:'',
                meanShow: false,
                maskShow:false,
                lotteryShow:false,
                fudisable:false,
                info:{
                    parent:'',
                    deposit:'',
                    sum:0,
                },
                user:{
                    k1:0,
                    k2:0,
                    k3:0,
                    k4:0,
                    t1:0,
                    t2:0,
                    t3:0,
                    t4:0,
                    r1:0,
                    r2:0,
                    r3:0,
                    r4:0,
                    p1:0,
                    p2:0,
                    p3:0,
                    p4:0,
                },
            }
        },
        created: function () {

        },
        async mounted() {
            this.lang = sessionStorage.getItem('lang')?sessionStorage.getItem('lang'):'en';
            this.p_address = this.$route.query.p_address?this.$route.query.p_address:this.getUrlParam('p_address');
            this.lang_list = this.en;
            switch(this.lang){
                case 'zh':
                    this.lang_list = this.zh;
                    break;
                case 'en':
                    this.lang_list = this.en;
                    break;
            }
            this.checkNet();
            var wow = new WOW(
                {
                    boxClass: 'wow',      // animated element css class (default is wow)
                    animateClass: 'animated', // animation css class (default is animated)
                    offset: 0,          // distance to the element when triggering the animation (default is 0)
                    mobile: true,       // trigger animations on mobile devices (default is true)
                    live: true,       // act on asynchronously loaded content (default is true)
                    callback: function (box) {
                        // the callback is fired every time an animation is started
                        // the argument that is passed in is the DOM node being animated
                    },
                    scrollContainer: null // optional scroll container selector, otherwise use window
                }
            );
            wow.init();
        },
        methods: {
            async profitSubmit(event,index){
                let _this = this;
                if(_this.fudisable){
                    return false;
                }
                _this.fudisable = true;
                let toggle = event.currentTarget;
                $(toggle).css('background','#dddddd');
                try{
                    const gasPrice = await provider.getGasPrice();
                    let res = await conContract.pickAllDjt(index,{
                        gasPrice: gasPrice,
                        gasLimit:800000
                    });
                    if(res){
                        _this.fudisable = false;
                        _this.$toast('操作成功');
                        setTimeout(function () {
                            location.reload();
                        },3000)
                    }
                }catch (e) {
                    _this.$toast('操作失败');
                    _this.fudisable = false;
                    setTimeout(function () {
                        location.reload();
                    },3000)
                }
            },
            todian(){
                window.open('https://t.me/DJT_Group','_self');
            },
            toTwitte(){
                window.open('https://twitter.com/DJTsystem','_self');
            },
            toTrade(){
                window.open('https://pancakeswap.finance/swap?outputCurrency=0x41D8675E6C64939c3245908EEaf827A995Fc47Ff&inputCurrency=0x55d398326f99059ff775485246999027b3197955&utm_source=tokenpocket','_self');
            },
            toWhite(){
                window.open('/static/djt.pdf');
            },
            toIndex(){
                this.$router.push({
                    name: 'home',
                    params: {

                    }
                });
                this.meanShow = false;
            },
            toOrder(){
                this.meanShow = false;
            },
            toInvite(){
                this.$router.push({
                    name: 'myInvite',
                    params: {

                    }
                })
                this.meanShow = false;
            },
            switchLang(type){
                switch(type){
                    case 'zh':
                        sessionStorage.setItem('lang','zh');
                        break;
                    case 'en':
                        sessionStorage.setItem('lang','en');
                        break;
                }
                location.reload();
            },
            //显示菜单
            showLang(){
                if(this.langShow){
                    this.langShow = false;
                }else{
                    this.langShow = true;
                }

            },
            //显示菜单
            showMask(){
                this.maskShow = true;
            },
            //关闭菜单
            closeMask(){
                this.maskShow = false;
            },
            //显示菜单
            showMean(){
                this.meanShow = true;
            },
            //关闭菜单
            closeMean(){
                this.meanShow = false;
            },
            connectWallet() {
                if (this.address) {
                    this.short_address = this.address.substr(0, 3) + '...' + this.address.substr(this.address.length - 3)
                } else {
                    this.checkNet()
                }
            },
            getUrlParam(name) {
                var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //构造一个含有目标参数的正则表达式对象
                var r = window.location.search.substr(1).match(reg);  //匹配目标参数
                if (r != null) return unescape(r[2]);
                return null; //返回参数值
            },
            async checkNet() {
                let _this = this;

                try {
                    provider = new ethers.providers.Web3Provider(web3.currentProvider);
                    signer = provider.getSigner();
                    _this.checkLogin();
                } catch (e) {
                    _this.$toast(_this.lang_list.index_warn);
                }

            },
            async checkLogin() {
                let _this = this;
                setInterval(function () {
                    window.ethereum.enable()
                        .then(function (accounts) {
                            if (accounts.length == 0 && defaultAccount != "") {
                                window.location.reload();
                                return;
                            }
                            if (accounts.length == 0) {
                                window.location.reload();
                                return;
                            }
                            if (accounts[0] != defaultAccount) {
                                defaultAccount = accounts[0];
                                window.location.reload();
                                console.log("account change, start bat updateData!")
                            }
                        })
                        .catch(function (reason) {
                            // Handle error. Likely the user rejected the login:
                            console.log(reason === "User rejected provider access")
                        })
                }, 1000);
                window.ethereum.enable()
                    .then(async function (accounts) {
                        if (accounts.length == 0) {
                            console.log('33333');
                            return;
                        }
                        defaultAccount = accounts[0];
                        console.log("defaultAccount: ", defaultAccount);
                        networkVersion = window.ethereum.networkVersion
                        if (networkVersion == 1) {  //以太坊主网
                            _this.type = 'ETH';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 42) {   //以太坊测试网  K
                            _this.type = 'ETH';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 128) {  //HECO主网
                            _this.type = 'HT';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 256) { //HECO测试网
                            _this.type = 'HT';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 56) { //币安主网
                            _this.type = 'BNB';
                        }
                        else if (networkVersion == 97) { //币安测试网
                            _this.type = 'BNB';
                        }
                        else {
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        conContract = new ethers.Contract(_this.api.conAddress,conAbi, signer);
                        if (defaultAccount != '') {
                            _this.address = defaultAccount;
                            _this.getInfo();
                        }
                    });
                setTimeout(function () {
                    if (defaultAccount) {
                        _this.address = defaultAccount;
                        _this.short_address = _this.address.substr(0, 3) + '...' + _this.address.substr(_this.address.length - 3)
                    }

                }, 500)
            },
            async getInfo(){
                let _this = this;
                await conContract.myInfo().then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.info.parent = res[0];
                    _this.info.deposit = parseFloat(ethers.utils.formatEther(res[1])*1000000000).toFixed(2);
                }).catch(err => {
                    console.log(err);
                    return;
                });

                await conContract.myKeys(0).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    console.log(res);
                    _this.user.k1 = parseInt(ethers.utils.formatEther(res)*1000000000000000000);
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.myKeys(1).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    console.log(res);
                    _this.user.k2 = parseInt(ethers.utils.formatEther(res)*1000000000000000000);
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.myKeys(2).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    console.log(res);
                    _this.user.k3 = parseInt(ethers.utils.formatEther(res)*1000000000000000000);
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.myKeys(3).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    console.log(res);
                    _this.user.k4 = parseInt(ethers.utils.formatEther(res)*1000000000000000000);
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.gameKeys(0).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    console.log(res);
                    _this.user.t1 = parseInt(ethers.utils.formatEther(res)*1000000000000000000);
                    if(_this.user.t1>0){
                        _this.user.r1 = parseInt(parseInt(_this.user.k1)/parseInt(_this.user.t1)*100);
                    }
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.gameKeys(1).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    console.log(res);
                    _this.user.t2 = parseInt(ethers.utils.formatEther(res)*1000000000000000000);
                    if(_this.user.t2>0){
                        _this.user.r2 = parseInt(parseInt(_this.user.k2)/parseInt(_this.user.t2)*100);
                    }
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.gameKeys(2).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    console.log(res);
                    _this.user.t3 = parseInt(ethers.utils.formatEther(res)*1000000000000000000);
                    if(_this.user.t3>0){
                        _this.user.r3 = parseInt(parseInt(_this.user.k3)/parseInt(_this.user.t3)*100);
                    }
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.gameKeys(3).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    console.log(res);
                    _this.user.t4 = parseInt(ethers.utils.formatEther(res)*1000000000000000000);
                    if(_this.user.t4>0){
                        _this.user.r4 = parseInt(parseInt(_this.user.k4)/parseInt(_this.user.t4)*100);
                    }
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.myRewards(0).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.user.p1 = parseFloat(ethers.utils.formatEther(res)*1000000000).toFixed(4);

                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.myRewards(1).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.user.p2 = parseFloat(ethers.utils.formatEther(res)*1000000000).toFixed(4);

                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.myRewards(2).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.user.p3 = parseFloat(ethers.utils.formatEther(res)*1000000000).toFixed(4);

                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.myRewards(3).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.user.p4 = parseFloat(ethers.utils.formatEther(res)*1000000000).toFixed(4);

                }).catch(err => {
                    console.log(err);
                    return;
                });
            },
        }
    }
</script>


